
  @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500;600;700;800;900&display=swap');
*{
  margin: 0;
  padding: 0;
  font-family: Poppins,Arial, Helvetica, sans-serif;
  box-sizing: border-box;
}
.start{
  margin-bottom: 1rem;
}
.logo{
  margin-top: 3rem;
  text-align: center;
  font-size: 1.8rem;
  font-weight: 700;
  background: #b30065;
  background: linear-gradient(to right, #ff0c96 0%, #CF8300 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.create{
  text-align: center;
  font-size: 1.6rem;
}
.login{
  text-align: center;
  margin-bottom: 3rem;
}
.inputfield{
  font-size: 1rem;
  border-radius: 1rem;
  border-width:thin;
  border: 1px solid black;
  padding-left: 4%;
  padding: 1rem;
  width: 90%;
  margin-bottom: 1rem;
}
.subbut{
  font-size: 1.4rem;
  width: 90%;
  padding: 0.8rem;
  border-radius: 1rem;
  border: none;
  background-color: orangered;
  color: white;
  cursor: pointer;
  transition: 0.5s;
}
.subbut:disabled{
  opacity: 0.8;
  cursor: no-drop;
  background-color: grey;
}
.oppar{
  text-align: center;
  margin: 2rem 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.Continue{
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
  border:2px solid black;
  border-radius: 2rem;
  margin-bottom: 1rem;
  width: 90%;
  cursor: pointer;
}
.textinbutt{
  text-decoration: none;
  font-size: 1.3rem;
  padding: 0.7rem 0.5rem;
}
.linktoaccount{
  text-decoration: none;  
  text-align: center;
}
.account-already{
  font-size: 1.2rem;
}

.moreOption{
  font-size: 0.8rem;
  font-weight: 100;
  margin-bottom: 2rem;
  cursor: pointer;
}
.header{
  display: flex;
  width: 90%;
  margin: 0 auto;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 2rem;
}
.icon-container{
  min-width: 60px;
  display: flex;
  justify-content: space-between;
}
/* .back-icon{
  font-size: 50px;
} */
ion-icon {
  font-size: 24px;
}
.header-detail{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header-name{
  font-size: 1.3rem;
  font-weight: bold;
}
.user-sec{
  display: flex;
  justify-content: start;
  align-items: center;
}
.profile-pic{
  width: 140px;
}
.profile-img{
  width: 100%;
}
.name{
  font-weight: bold;
  font-size: 1.2rem;
  text-align: start;
}
.work{
  font-size: 0.8rem;
  text-align: start;
}
.location{
  font-size: 0.7rem;
  text-align: start;
  color: grey;
}
.header-bio{
  color:rgb(26, 26, 26);
  text-align: start;
  padding-left: 3%;
}
.header-biotwo{
  color:rgb(26, 26, 26);
  text-align: start;
  font-weight: bold;
  font-size: 0.8rem;
  padding-left: 3%;
  margin-bottom: 0.5rem;
}
#biospan{
  color: grey;
}
.perks{
  display: flex;
  margin-bottom: 1rem;
}
.perksbutt{
  background-color: rgb(230, 230, 230);
  border: none;
  border-radius: 1rem;
  font-size: 0.8rem;
  padding: 10px 15px;
  margin-left: 1rem;
}
.contact-butt{
  font-size: 1rem;
  background-color: orangered;
  border: none;
  border-radius: 1rem;
  padding: 10px;
  color: white;
  margin: 10px;
}
.add-butt{
  font-size: 0.8rem;
  background-color: rgb(255, 234, 226);
  border: none;
  border-radius: 1rem;
  padding:  10px 1rem;
  color: orangered;
}
.active{
  text-align: start;
  margin-left: 5%;
  color: green;
  margin-bottom: 2rem;
}
.contact-sec{
  display: flex;
  align-items: center;
  justify-content: start;
  gap: 1rem;
  flex-wrap: wrap;
}
.instasection{
  display: flex;
  justify-content: start;
  margin-bottom: 1rem;
}
.perksbuttinsta{
  background-color: rgb(230, 230, 230);
  border: none;
  border-radius: 1rem;
  font-size: 0.8rem;
  padding: 10px 15px;
  margin-left: 0.4rem;
}
.auth-pic-div{
  height: 140px;
  width: 140px;

}
.auth-img{
  width: 100%;
}
.auth-sec{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
}
#bold{
  font-weight: bold;
}
.insta-go{
  margin-left: 5%;
  text-align:start;
}
.insta-go-two{
  margin-left: 5%;
  text-align:start;
  color: rgb(34, 74, 255);
  cursor: pointer;
}
.insta{
  margin-bottom: 2rem;
}
.activity{
  margin-left: 3%;
  margin-bottom: 1rem;
}
.spanned{
  font-weight: bolder;
}

.hrr{
  width: 94%;
  margin: 10px auto;
  border-top: 0.1px solid grey;
}
.pics-filter{
  display: flex;
  justify-content: space-around;
  margin-bottom: 1rem;
}
.filter-butt{
  border: 0.1px solid grey;
  padding: 0.5rem 1rem;
  border-radius: 1rem;
  font-size: 12px;
}
.image-div{

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  margin-bottom: 4rem;
}
.pictures{
  background-color: rgb(216, 216, 216);
  width: calc(90%/2);
  height: 160px;
}
.predict{
  text-align: center;
  font-weight: bold;
  margin-bottom: 1rem;
}
.butt-predict{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 1rem;
}
.predict-now{
  color: rgb(0, 162, 255);
  text-align: center;
  text-decoration: underline;
  cursor: pointer;
}

.predicter{
  margin-bottom: 4rem;
}
#logout{
  cursor: pointer;
}
button{
  cursor: pointer;
}
ion-icon{
  cursor: pointer;
}
.app-status{
  width: 90%;
  margin:0 auto;
  display: flex;
  align-items: center;
  margin-top: 2rem;
  margin-bottom: 0.8rem;
}
.app-status-text{
  font-size: 1.15rem;
  font-weight: 600;
}
.task-box{
  width: 90%;
  margin:0 auto;
  display: flex;
  gap:1rem;
  align-items: center;
  margin-bottom: 1rem;
}
.task{
  font-size: 16px;
  font-weight: 600;
}
.task-img{
  height: 100%;
  width: 100%;
  object-fit: fill;
  transform: scale(2);
}
.task-imagebox{
  overflow: hidden;
  border-radius: 100%;
  width: 50px;
  height: 42px;
}
.camp-chat{
  display: flex;
  align-items: center;
  width: 90%;
  margin: 0 auto;
  justify-content: space-between;
  margin-bottom: 2.2rem;
}
.campaignn{
  color:dodgerblue;
  font-size: 0.94rem;
}
.chat-text{
  color:dodgerblue;
  font-weight: 600;
  font-size: 0.94rem;

}
#chat-icon{
  font-size: 20px;
}
.progress{
  width: 90%;
  margin:0 auto;
  margin-bottom: 2rem;
  position: relative;
  padding-left: 1.5rem;
  border-left: 2.5px solid black;

}
.progres-title{
  font-weight: bold;
  font-size: 1rem;
}
.progress-detail{
  font-size: 0.85rem;
}
.progress-time{
  color: grey;
  font-size: 0.75rem;
  font-weight:lighter;
}
.nego{
  margin-bottom: 1rem;
}

#circle{
  height: 10px;
  width: 10px;
  background-color: rgb(0, 0, 0);
  border-radius: 50%;
  display: inline-block;
  position: absolute;
  left: -7px;
}
.reject-text{
  text-align: center;
  color: grey;
  font-size: 0.85rem;
  text-decoration: underline;margin-bottom: 3rem;
}
.read{
  color: dodgerblue;
  text-align: center;
  font-size: 0.86rem;
  margin-bottom: 1rem;
}
.acceptall{
  padding: 0.8rem 1.2rem;
  color: orangered;
  font: 1.1rem;
  border-radius: 0.4rem;
  border: 0.5px solid orangered;
  margin-bottom: 2rem;
}
.accept-allbox{
  text-align: center;
}
.mobclass{
  max-width: 400px;
  margin: 0 auto;
}
.navbar{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.nav-list{
  display: flex;
  gap: 2rem;
  padding-right: 1rem;
  font-size: 0.9rem;
  font-weight: 600;
  align-items: center;
}
.nav-link{
  list-style-type: none;
  display: flex;
  align-items: center;
  gap: 0.2rem;
}
.logo-class{
  padding-left: 1rem;
}
#nav-icons{
  font-size: 20px;
}
.wide-box{
  display: flex;
  height: 100vh;
}
.content-box{
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.sidebar-box{
  flex:0.05;
  display: flex;
  flex-direction: column;
  transition: all 0.3s ease-in-out !important;
}
.sidebar-box.expand{
  flex:0.16;
}
.side-content{
  display: flex;
  flex-direction: column;
  flex: 0.8;
}
.upper{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 0.06;
}
.lower{
  display: flex;
  flex-direction: column;
  flex: 1;
}
.lower-upper{
  display: flex;
  flex-direction: column;
  align-items: center;
  flex:0.5;
  gap: 1rem;
}
.lower-lower{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  flex:0.5;
}
#sidebar-icon{
  font-size: 18px;
  padding-top: 1rem;
}
#sidebar-icon-top{
  font-size: 28px;
}
.task-details{
  display: flex;
  gap: 1rem;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}
.task-title{
  padding-left: 1rem;
  display: flex;
  align-items: center;
}
.task-title-text{
  font-size: 0.8rem;
}
.task-option-buttons{
  border: 0.5px solid grey;
  border-radius: 1rem;
  padding: 4px 8px;
  font-size: 12px;
  font-weight: 600;
}
.task-back{
  padding-right: 1rem;
  cursor: pointer;
  font-weight: 700;
  color: orangered;
}
.suggest{
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 2rem;

}
.pred-text{
  font-size: 0.9rem;
  padding-left: 1rem;
}
.suggest-inf{
  border-radius: 1rem;
  padding: 2px 4px ;
  font-size: 10px;
  font-weight: 600;
  border: 0.5px solid grey;
  color: grey;
}
.suggest-box-outer{
  display: flex;
  justify-content: center;
}
.suggest-box-inner{
  display: flex;
  flex: 0.6;
  gap: 1rem;
  flex-wrap: wrap;
}
.suggest-box{
  min-width: 250px;
  flex: 0.3;
  border: 0.5px solid grey;
  border-radius: 1rem;
}
.suggest-title{
  padding: 0.4rem;
  padding-left: 1rem;
  font-size: 12px;
  font-weight: 600;
  background-color: rgb(210, 244, 255);
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
  border-bottom: 0.5px solid grey;
}
.suggest-content{
  padding: 2.8rem 1rem;
}
.scttext{
  color: rgb(0, 48, 87);
  text-align: center;
  font-weight: 600;
}
.sctdes{
  font-size: 0.6rem;
  color: rgb(120, 159, 168);
  text-align: center;
}
.dashboard{
  border-radius: 1rem;
  padding: 1rem 2rem;
  color: lightslategray;
  border: 0.5px solid lightslategrey;
  flex: 0.5;
  margin-bottom: 1rem;
  cursor: pointer;
}